html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
}
a {
  color: inherit;
  text-decoration: none;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
body {
  background-color: var(--background);
  font-family: 'WorkSans', sans-serif;
  font-weight: 400;
}
::-webkit-scrollbar {
  width: 0;
}

:root {
  --primary: rgba(17, 106, 204, 1);
  --background: rgba(255, 255, 255, 1);
  --grey: rgba(243, 244, 246, 1);
  --black: rgb(18, 17, 27);
  --border-gray: rgba(156, 163, 175, 1);
  --light-blue: rgba(179, 206, 226, 1);
  --hover-blue: rgba(160, 195, 255, 0.652);
  --blue: rgb(148, 187, 255);
  --focus-blue: rgb(6, 55, 106);
  --font-dark: rgba(24, 34, 51, 1);
  --light-blue: rgba(0, 82, 255, 0.1);
  --green: rgba(118, 202, 102, 1);
  --red: rgba(253, 78, 93, 1);
  --back-grey: rgba(229, 229, 229, 1);
}
.container {
  max-width: 1440px;
  padding: 0 50px;
  margin: auto;
}
.header {
  padding: 20px 0;
  background-color: var(--primary);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25);
}
.header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
input {
  transition: all 0.3s ease-in-out;
}
input:hover {
  border-color: var(--primary);
}
input:focus {
  border-color: var(--primary);
  background-color: var(--hover-blue);
}
.header__btns {
  order: 2;
  display: flex;
  gap: 20px;
  white-space: nowrap;
}
.header__btn {
  background-color: var(--background);
  color: var(--primary);
  font-weight: 700;

  box-sizing: border-box;
  transition: background-color 0.3s ease-in-out;
}
.header__btn:hover {
  background-color: var(--hover-blue);
}
.header__btn:focus {
  background-color: var(--hover-blue);
}
.header__btn:active {
  background-color: var(--hover-blue);
}
.coin__logo {
  order: 1;
  font-weight: 300;
  font-size: 48px;
  line-height: 56.3px;
  color: var(--background);
}
.btn {
  cursor: pointer;
  border: none;
  padding: 14px 24px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  width: fit-content;
  border-radius: 7px;
}
@media (max-width: 690px) {
  .header__btns {
    /* max-width: 690px; */
    width: 100%;
    overflow-x: scroll;
    align-items: center;
    justify-content: center;
  }
  .coin__logo {
    margin-bottom: 15px;
    /* margin-left: 40px; */
  }
  .header .container {
    padding: 0;
  }
  .header__container {
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 576px) {
  .container {
    padding: 0 20px;
  }
  /* .header__btns{
    padding: 0 20px;
  } */
}
@media (max-width: 490px) {
  .bank__btn {
    margin-left: 80px;
  }
  .ex__btn {
    margin-right: 20px;
  }
}
@media (max-width: 400px) {
  .bank__btn {
    margin-left: 150px;
  }
}
